/*
 *  Report:
 * -----------
 */
body{
  background-color: #fff;
  position: relative;
  margin: 0;
}
.wrapper {
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
}
.wrapper:before, .wrapper:after {
  content: " ";
  display: table;
}

section {
  display: block;
}

.report {
  position: relative;
  margin: 10px;
  font-size: 14px;
}

.report-header {
  margin: 10px 0 10px 0;
  font-size: 20px;
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
  text-align: center;
}

.report-info {

}

.institute-name {
  font-size: 25px;
  font-weight: bold;
}
.institute-address {
  font-size: 15px;
  font-weight: inherit;
}
.report-title {
  font-size: 18px;
  font-weight: inherit;
}
.report-subtitle {
  font-size: 15px;
  font-weight: inherit;
}
.report-header .col-xs-12{
  position: relative;
}
.report-header .print-date {
  top: 0;
  right: 10px;
  position: absolute;
}

.report-body {

}
.report-body .report-filter {
  text-align: left;
}

.report-body .report-filter .filter-text {
  font-weight: bold;
}
.report-body .report-filter .filters {
  font-size: inherit;
}

.report-body .report-data {
  margin-bottom: 50px;
}

table {
  width: 100%;
  max-width: 100%;
}

table {
  background-color: transparent;
  table-layout: fixed;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.report-body .report-data table.main-data tr th {
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  vertical-align: bottom;
  border-bottom: 2px solid #ddd;
  padding: 8px;
}

.report-body .report-data table.main-data tr td {
  text-align: center;
  font-size: 10px;
  padding: 8px;
  vertical-align: top;
  border-top: 1px solid #ddd;
}

.report-body .report-data table.classic tr th {
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  vertical-align: middle;
  border: 1px solid #000;
  padding: 1px;
}

.report-body .report-data table.classic tr td {
  text-align: center;
  font-size: 10px;
  vertical-align: middle;
  border: 1px solid #000;
  padding: 1px;
}

.report-authority .col-xs-4 {
  padding-top: 20px;
}

.report-authority h5{
  text-align: center;
}

.report-footer {
  font-size: 10px;
  position: fixed;
  bottom: 0;
  height: 30px;
  width: 100%;
  padding:10px;
}

.report-footer .left{
  text-align: left;
  vertical-align: bottom;
}
.report-footer .right{
  text-align: right;
  vertical-align: bottom;
}

.red, .weekend {
  color: #ff0000;
}
.green {
  color: green;
}

.holiday {
  color: #983d8b;
}
/*
 * Misc: print
 * -----------
 */
@media print {
  body {
    margin: 0;
    padding: 0;
  }
  .no-print {
    display: none !important;
  }

  @page {
    size:  A4 portrait;
    margin: 2mm;
    margin-bottom: 20px;
  }

  * {
    -webkit-print-color-adjust: exact !important; /* Chrome, Safari */
    color-adjust: exact !important; /*Firefox*/
  }
}


